body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
background-color: #d9edf2;
  height: 100vh;
}

.container {
  padding: 50px;
  max-width: 600px;
  margin: 0 auto;
  min-width: 575px;
}


.App-header {
  font-family: 'Roboto', sans-serif;
  color: #585858;
}


button {
  font-family: 'Roboto', sans-serif;
  display: inline-block;
  position: relative;
  border-radius: 50px;
  background: #868478;
  width: 180px;
  height: 50px;
  position: relative;
  color: #fff;
  font-size: 0.97em;
  letter-spacing: 0.094em;
  cursor: pointer;
  box-shadow: 10px 10px 14px 1px rgba(00,00,00,0.2);
  text-align: center;
  transition: all 0.5s;
  text-transform: uppercase;
  opacity: 1;
  border: none;
}

button:hover {
  box-shadow: none;
}

button#button2 {
  float: right;
}

svg.donutchart g.donutchart-legend .donutchart-legend-item-label, svg.donutchart g.donutchart-innertext {
  font-family: 'Roboto', sans-serif;
  color: #585858;
}

